"use client";

import React from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import { FaFilm, FaSearch, FaHeart, FaHome } from "react-icons/fa";
import { useFavourites } from "../../features/favourites/context";

export default function Header() {
  const pathname = usePathname();
  const { favourites } = useFavourites();

  const isActive = (path) =>
    pathname === path ? "header__link active" : "header__link";

  return (
    <header className="header">
      <div className="container">
        <Link href="/" className="header__logo">
          <FaFilm />
          <span>КиноПоиск</span>
        </Link>

        <nav className="header__nav">
          <Link href="/" className={isActive("/")}>
            <FaHome />
            <span>Главная</span>
          </Link>
          <Link href="/search" className={isActive("/search")}>
            <FaSearch />
            <span>Поиск</span>
          </Link>
          <Link href="/favourites" className={isActive("/favourites")}>
            <FaHeart />
            <span>Избранное</span>
            {favourites?.length > 0 && (
              <span className="header__badge">{favourites.length}</span>
            )}
          </Link>
        </nav>
      </div>
    </header>
  );
}
